@import url(https://fonts.googleapis.com/css?family=Montserrat);

pre,
code {
  font-family:  Menlo, Monaco, "Courier New", monospace;
}

pre {
    padding: 0.5rem;
    line-height: 1.25;
    overflow-x: scroll;
}

a.nounder {
    border-bottom: none;
    text-decoration: none;
}

a.nounder:hover {
    border-bottom: 2px solid #752FFF;
    padding-bottom: 2px;
    text-decoration: none;
}

a,
a:visited {
    color: #444;
}

a.paper,
a.paper:visited {
    color: #752FFF;
    text-decoration: none;
}

.modest-no-decoration {
    text-decoration: none;
}

html {
    font-size: 12px;
}
@media screen and (min-width: 32rem) and (max-width: 48rem) {
    html {
        font-size: 15px;
    }
}
@media screen and (min-width: 48rem) {
    html {
        font-size: 16px;
    }
}

body {
    line-height: 1.85;
}

.modest-p,
p {
    font-size: 100%;
    margin-bottom: 1.3 rem;
}

.modest-h1,
.modest-h2,
.modest-h3,
.modest-h4,
h1,
h2,
h3,
h4 {
    margin: 1.414rem 0 0.5rem;
    font-weight: inherit;
    line-height: 1.42;
    letter-spacing: 1.5px;
}

.modest-h1,
h1 {
    margin-top: 0;
    font-size: 3.998rem;
}

.modest-h2,
h2 {
    font-size: 2.827rem;
}

.modest-h3,
h3 {
    font-size: 1.999rem;
}

.modest-h4,
h4 {
    font-size: 1.414rem;
    letter-spacing: 1.5px;
    letter-spacing: 1.5px;
}

.modest-h5,
h5 {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 0;
}

.modest-h6,
h6 {
    font-size: 0.88rem;
    letter-spacing: 1.5px;
}

.modest-small,
small {
    font-size: 0.707em;
}
/* https://github.com/mrmrs/fluidity */
canvas,
iframe,
img,
select,
svg,
textarea,
video {
    max-width: 100%;
}
