@import url(http://fonts.googleapis.com/css?family=Arimo:700,700italic);*/


html {
    font-size: 18px;
    max-width: 100%;
}

body {
    color: #444;
    font-family: 'Open Sans Condensed', sans-serif;
    // font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    margin: 0 auto;
    max-width: 48rem;
    line-height: 1.45;
    padding: 30px;
}

.section {
    font-size: 65%;
    font-weight: 700;
    letter-spacing: 2px;
    padding-top: 10px;
}

.title {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 17px;
    padding-top: 0;
}

.title-middle {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 6px;
    padding-top: 0;
}

.title-right {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 15px;
    padding-top: 0;
}

.title-right2 {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 15px;
    padding-top: 0;
}


.title2 {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 2px;
    padding-top: 6px;
}

.title3 {
    font-size: 80%;
    font-weight: 700;
    letter-spacing:1.5px;
    padding-bottom: 7px;
    padding-top: 0;
}

.titlepapers {
    font-size: 80%;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding-bottom: 7px;
    padding-top: 0;
}

.underline {
    border-bottom: 2px solid #000;
    padding-bottom: 2px;
}

.papers {
    font-size: 90%;
    font-weight: 400;
}

#main {
    margin-left: 230px;
    margin-right: 190px;

}

.blurb_text {
    font-size: 70%;
    padding-bottom: 0.05px;
}

.blurb_text_events {
    font-size: 70%;
    padding-bottom: 0.05px;
}

#blurb {
    float: left;
    max-width: 184px;

}

#events {
    float: right;
    max-width: 184px;
}

@media screen and (max-width:600px) {
    #main{
      margin-left: 0px;
      margin-right: 0px;
    }

    #parent{
      display:flex;
      flex-flow: column;
    }

    #main{order:2;}
    #blurb{order:1;}
    #events{order:3;}


    #events, #blurb {
      float: none;
      max-width: none;
    }
    .title-right{
      padding-top: 40pt
    }

    #blurb_text{
      font-size: 100%;
      margin-right: 0px;
    }
}

@media only screen
  and (max-device-width: 568px)
  {
    .html{
      font-size: 16px;
    }
    #main{
      margin-left: 0px;
      margin-right: 0px;
    }

    #parent{
      display:flex;
      flex-flow: column;
    }

    #main{order:2;}
    #blurb{order:1;}
    #events{order:3;}


    #events, #blurb {
      float: none;
      max-width: none;
    }


    #blurb_text{
      padding-bottom: none;
      font-size: 140%;
      margin-right: none;
    }
    .title, title2, title3, titlepapers{
      font-size: 140%
    }

    .title-right{
      padding-top: 20pt
    }
    #papers{
      font-size: 120%
    }
}

#nav {
    background-color: #333;
}

.active {
    background-color: #6E6E6E;
    color: black;
}

ul.navigation {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    /*background-color: #333;*/
    max-width: 49.7rem;
    margin: auto;
}

li.navigation {
    float: left;
}

.dropbtn,
li a.navigation {
    font-size: 0.85rem;
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.dropdown:hover .dropbtn,
li a.navigation:hover {
    background-color: #3498db;
}

li.dropdown {
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.dropdown-content a {
    color: black;
    font-size: 0.85rem;
    padding: 8px 12px;
    text-decoration: none;
    display: block;
    text-align: left;
}

.dropdown-content a:hover {
    background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
    display: block;
}


h1,
h2,
h3 {
    border-bottom: 2px solid #fafafa;
    margin-bottom: 1.15rem;
    padding-bottom: 0.5rem;
    text-align: center;
}

blockquote {
    border-left: 8px solid #fafafa;
    padding: 1rem;
}

code,
pre {
    background-color: #fafafa;
}

footer {
    bottom: 0;
    left: 0;
    position: fixed;
    text-align: center;
    width: 100%;
    z-index: -1;
}
